<template>
  <div>
    <el-menu
      mode="horizontal"
      class="el-menu-demo"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item v-for="ss in ssList" :key="ss.name" @click="loadjs(ss.componentName)" :index="ss.componentName" >
        <i class="el-icon-menu"></i>
        <span slot="title">{{ ss.name }}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'menulist',
  data () {
    return {
      ssList: []
    }
  },
  created: function () {
    this.ssList = window.menuList
  },
  methods: {
    loadjs: function (modName) {
      if (modName) {
        console.log(modName)
        var self = this
        window.require([modName],
          function (component) {
            window.Vue.component(modName, component)
            var ss = []
            ss.push({ path: '/' + modName, component: window.Vue.component(modName) })
            self.$router.addRoutes(ss)
            self.$router.push(modName)
          }
        )
      }
    }
  }
}
</script>



