<template>
  <div>
    <ul>
      <li v-for="ss in ssList" :key="ss.name">
        {{ ss.serviceName }}
        <ul v-if="ss.menuList">
          <li v-for="menu in ss.menuList" :key="menu.name" @click="loadjs(menu.componentName, ss.serviceName)">
            {{ menu.name }}
            <ul v-if="menu.child">
              <li v-for="submenu in menu.child" :key="submenu.name" @click="loadjs(submenu.componentName, ss.serviceName)">
                {{ submenu.name }}
              </li>
            </ul>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'sidebar',
  data () {
    return {
      ssList: [
        { serviceName: 'user-admin',
          menuList: [
            { name: 'Level1-1', componentName: 'level1' },
            { name: 'Level1-2', componentName: 'level2' },
            { name: 'Level1-3', componentName: 'level3' }
          ]
        },
        { serviceName: 'test-admin',
          menuList: [
            { name: 'Test1-1',
              child: [
                {name: 'Test1-1', componentName: 'test1'},
                {name: 'Test1-2', componentName: 'test2'},
                {name: 'Test1-3', componentName: 'test3'}
              ]
            }
          ]
        }
      ]
    }
  },
  methods: {
    loadjs: function (modName, serviceName) {
      if (modName) {
        console.log(modName)
        var self = this
        window.require.config({
          // change url here!!
          // baseUrl: '/' + serviceName + '/js'
          baseUrl: 'http://localhost:8080' + '/js'
        })
        window.require([modName],
          function (component) {
            window.Vue.component(modName, component)
            var ss = []
            ss.push({ path: '/' + modName, component: window.Vue.component(modName) })
            self.$router.addRoutes(ss)
            self.$router.push(modName)
          }
        )
      }
      console.log('----next----')
    }
  }
}
</script>



