import axios from 'axios'
import merge from 'lodash/merge'
import qs from 'qs'

const http = axios.create({
  timeout: 1000 * 30,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json; charset=utf-8'
  }
})

/**
 * 响应拦截
 */
http.interceptors.response.use(response => {
  if (response.data === null || (response.data.code == null)) {
    return response
  }

  if (response.data && ((response.data.code === 401) || response.data.code === '888888')) { // 401, token失效
    window.app.$notify.error({
      title: '提示',
      message: '登录错误'
    })
  } else if (response.data.code === '777777') { // 无权访问
    window.app.$notify.error({
      title: '提示',
      message: '无权访问'
    })
  } else if (response.data.code === '999999') { // 鉴权服务器异常
    window.app.$notify.error({
      title: '提示',
      message: '鉴权服务器异常'
    })
  } else if (
    response.data.code === '101101' ||
    response.data.code === '102101' ||
    response.data.code === '500000') { // 其他错误
    window.app.$notify.error({
      title: '错误',
      message: response.data.msg
    })
  }
  return response
}, error => {
  if (error.response.status === 500) {
    let msg = ''
    if (error.response.data) {
      msg = error.response.data
      if (error.response.data.msg) {
        msg = error.response.data.msg
      }
    }

    window.app.$notify.error({
      title: '提示',
      message: '服务器出错了 : ' + msg
    })
  }

  if (error.response.status >= 400 && status.code < 500) {
    window.app.$notify.error({
      title: '提示',
      message: '哪去了？ 404'
    })
  }
  return Promise.reject(error)
})

http.interceptors.request.use(function (config) {
  var url = config.url
  // console.log(url)
  var idx = 0
  for (var i = 0; i < url.length; i++) {
    if (i > 0 && url.charAt(i) === '/') {
      idx = i
      break
    }
  }
  config.url = config.url.slice(idx)
  return config
})

/**
 * get请求参数处理
 * @param {*} params 参数对象
 * @param {*} openDefultParams 是否开启默认参数?
 */
http.adornParams = (params = {}, openDefultParams = true) => {
  var defaults = {
    't': new Date().getTime()
  }
  return openDefultParams ? merge(defaults, params) : params
}

/**
 * post请求数据处理
 * @param {*} data 数据对象
 * @param {*} openDefultdata 是否开启默认数据?
 * @param {*} contentType 数据格式
 *  json: 'application/json; charset=utf-8'
 *  form: 'application/x-www-form-urlencoded; charset=utf-8'
 */
http.adornData = (data = {}, openDefultdata = true, contentType = 'json') => {
  var defaults = {
    't': new Date().getTime()
  }
  data = openDefultdata ? merge(defaults, data) : data
  return contentType === 'json' ? JSON.stringify(data) : qs.stringify(data)
}

export default http



