import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import sidebar from '@/components/sidebar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: 'test',
      name: 'HelloWorld',
      component: {
        default: sidebar,
        content: HelloWorld
      }
    },
    {
      path: '/sidebar',
      name: 'sidebar',
      component: sidebar
    }
  ]
})



