<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <input type="text" v-model="msg"/>
    <h2>MMMMMMMMMMMM</h2>
    <button v-on:click="doThis">AAAA</button>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    doThis: function () {
      this.$api.get('/api/msg').then(response => { this.msg = response.data })
      // var a = [];
      // a.push(
      //   {
      //     path: '/aaaaa',
      //     component: require('@/components/Home.vue')
      //   });
      // this.$router.addRouters(a);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



